// @flow

import * as React from 'react';
import { Image as ImageBase, View } from 'react-native-web';
import { useDOMSize } from './Layout.js';

type Props = {|
  source: string,
  previewSource?: ?string,
  size: {| width: number, height: number |},
|};

export function Image({
  source,
  previewSource,
  size: { width, height },
}: Props) {
  let [size, ref] = useDOMSize();
  let [img, setImg] = React.useState();

  React.useEffect(() => {
    let img = new window.Image();
    img.src = source;
    img.decode().then(() => {
      setImg(source);
    });
    return () => {
      img.src = '';
      try {
        delete img.src;
      } catch (e) {
        // On Safari in Strict mode this will throw an exception,
      }
      img = null;
    };
  }, [source]);

  console.log(img);
  if (size == null) {
    return <View ref={ref} style={{ width: '100%' }} />;
  }
  let ratio = height / width;
  return (
    <ImageBase
      source={previewSource}
      style={{ width: size.width, height: size.width * ratio }}
      resizeMode="contain"
    />
  );
}
